import React from "react"
import imageURLs from "../../data/images-cds";
const isMobile = typeof window !== 'undefined' && window.innerWidth  < 480;
const bgImage = isMobile ? "Mobile" : "Desktop"


const HomeImage = () => {
  const imgInteger = Math.ceil(Math.random() * 4);
  return (
      <React.Fragment>
        <img
            src={imageURLs.Landing[bgImage][imgInteger]}
            alt=""
            className={`homeImage${bgImage}`}
        />
        <div className={`logoHomeDiv${bgImage}`}>
            <img src={imageURLs.Logos.white} className={`logoHome${bgImage}`} />
        </div>
      </React.Fragment> 
  )
}
//
//<div className="row margin-0" style={{ maxWidth: "100vw", marginBottom: "-2px"}}>
export default HomeImage;
